body, html {
    margin: 0;
    padding: 00;
    overflow: hidden;
    width: 100%;
    height: 100%;
  }
  
  #canvas-container {
    width: 100%;
    height: 100%;
    position:relative;
    grid-column: 3/4;
  }

  .container {
    display: grid;
    grid-template-columns: 50px 200px 1fr 200px;
    grid-template-rows:50px 50px 50px 50px 50px 50px 50px 10%;
  }

  .icon{
    padding: 0.5em;
    background-color: #fb7185;
    font-weight: 700;
    color: blueviolet;
    border: 3px solid black;
    border-radius: 2px;

  }

  .icon:hover {
    border-right-color:#fb7185;
    width:auto+21;
  }

  
  .icon-1 {
    grid-column-start: 1;
    grid-column-end:2;
    grid-row-start: 2;
    grid-row-end:3;
  }

  .icon-2 {
    grid-column-start: 1;
    grid-column-end:2;
    grid-row-start: 3;
    grid-row-end:4;
  }

  .icon-3 {
    grid-column-start: 1;
    grid-column-end:2;
    grid-row-start: 4;
    grid-row-end:5;
  }

  .icon-4 {
    grid-column-start: 1;
    grid-column-end:2;
    grid-row-start: 5;
    grid-row-end:6;
  
  }

  .icon-5 {
    grid-column-start: 1;
    grid-column-end:2;
    grid-row-start: 6;
    grid-row-end:7;
  }

  .icon-6 {
    grid-column-start: 1;
    grid-column-end:2;
    grid-row-start: 7;
    grid-row-end:8;
  
  }
  .sidebar {
    grid-column-start: 2;
    grid-column-end:3;
    grid-row-start: 2;
    grid-row-end:9;

  
  }



