body, html {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

#canvas-container {
  grid-column: 3 / 4;
  width: 100%;
  height: 100%;
  position: relative;
}

.container {
  grid-template-rows: 50px 50px 50px 50px 50px 50px 50px 10%;
  grid-template-columns: 50px 200px 1fr 200px;
  display: grid;
}

.icon {
  color: #8a2be2;
  background-color: #fb7185;
  border: 3px solid #000;
  border-radius: 2px;
  padding: .5em;
  font-weight: 700;
}

.icon:hover {
  width: auto21;
  border-right-color: #fb7185;
}

.icon-1 {
  grid-area: 2 / 1 / 3 / 2;
}

.icon-2 {
  grid-area: 3 / 1 / 4 / 2;
}

.icon-3 {
  grid-area: 4 / 1 / 5 / 2;
}

.icon-4 {
  grid-area: 5 / 1 / 6 / 2;
}

.icon-5 {
  grid-area: 6 / 1 / 7 / 2;
}

.icon-6 {
  grid-area: 7 / 1 / 8 / 2;
}

.sidebar {
  grid-area: 2 / 2 / 9 / 3;
}

